@import url(https://fonts.googleapis.com/css?family=Nunito|Titillium+Web|Inconsolata&display=swap&weights=lighter|bolder);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

code {
  font-family: "Inconsolata" !important; }

.App {
  font-family: "Nunito", sans-serif;
  font-weight: light;
  text-align: center;
  min-width: 1024px;
  background-color: #eaeaea;
  background: -webkit-gradient(linear, left bottom, left top, from(#FFB31F), to(#1fc2c2));
  background: -webkit-linear-gradient(bottom, #FFB31F 0%, #1fc2c2 100%);
  background: linear-gradient(0deg, #FFB31F 0%, #1fc2c2 100%); }

.App-header {
  z-index: 1;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  border-bottom: 1px solid #2d383a;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Titillium Web", sans-serif;
  font-weight: lighter;
  font-size: 34.5px;
  color: #2d383a;
  background-color: white; }
  .App-header h1 {
    margin-top: 0; }

.App-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .App-wrapper .App-main {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0px;
    padding-top: 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; }
  .App-wrapper .QueryManager {
    display: flex; }
    .App-wrapper .QueryManager .SchemaAndData {
      display: flex;
      flex-flow: row; }
      .App-wrapper .QueryManager .SchemaAndData .Data {
        display: flex;
        flex-flow: row; }
    .App-wrapper .QueryManager .TipsAndQuery {
      width: 100%;
      display: flex;
      flex-direction: row;
      align-items: flex-start; }
      .App-wrapper .QueryManager .TipsAndQuery .Tips {
        width: 200px;
        padding: 2px;
        flex-flow: grid; }
      .App-wrapper .QueryManager .TipsAndQuery .AeqlQuery {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-items: flex-start; }
        .App-wrapper .QueryManager .TipsAndQuery .AeqlQuery > * {
          display: flex; }

.Card {
  box-shadow: 3px 3px 5px #324;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 8px 50px; }
  .Card p {
    margin: 0;
    padding: 4px; }
  .Card code {
    font-size: 82.5%;
    margin-bottom: 10px; }

.Welcome {
  position: absolute;
  right: 0;
  top: -20px;
  padding: 50px;
  margin: 80px;
  opacity: 0.98; }
  @media only screen and (max-width: 1200px) {
    .Welcome {
      display: none; } }

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hide;
  margin: 18px;
  margin-top: 5px;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 0.6); }

table {
  border: 1px solid #eaeaea;
  color: #000;
  margin: 0 auto;
  background-color: #fefff3;
  padding: 1px;
  font-size: 12px; }
  table td {
    border: 1px solid #eaeaea;
    padding: 6px 14px;
    font-size: 10px;
    background-color: white; }

textarea {
  width: 90%;
  min-height: 140px;
  border: 1px solid gray;
  background-color: #303a3c;
  color: white;
  border-radius: 24px;
  padding: 22px;
  margin: 10px;
  font-family: "Inconsolata";
  font-size: 140%; }
  textarea:focus, textarea:hover {
    outline: 0px !important;
    -webkit-appearance: none;
    box-shadow: none !important; }

dl > * {
  -webkit-margin-start: 0;
          margin-inline-start: 0; }

dl dt {
  font-family: "Titillium Web";
  margin-top: 8px;
  padding: 10px; }

dl dd {
  font-weight: light;
  font-size: 12px;
  padding-bottom: 20px; }

h1, h2, h3, h4, h5, h6 {
  margin-bottom: 2px;
  margin-top: 0px;
  text-transform: uppercase;
  font-family: "Titillium Web"; }

h1 {
  font-size: 24px; }

h2 {
  font-size: 18px; }

h3 {
  font-size: 16px; }

h4 {
  font-size: 14px; }

h5 {
  font-size: 12px; }

.EntityTable table {
  padding: 5px; }

